import React,{useState} from "react";
import { Form,Button } from "react-bootstrap";
import { useNavigate } from "react-router";
import { useUserAuth } from "../context/UserAuthContext";
import "../styles/HomeStyles.css";



const Home = () => {
  

  const [contact, setContact] = useState({name:"",phone:""});
   
    const { logOut, user } = useUserAuth();
    const navigate = useNavigate();
    const handleLogout = async () => {
      try {
        await logOut();
        navigate("/login");
      } catch (error) {
        console.log(error.message);
      }
    };
   
   

    const handleChange = (e) =>{
      e.preventDefault();
 
        const {name, value} = e.target;
        setContact((prev) => {
          return{...prev, [name]: value};
        });
    };

     
    // const handleSubmit = (e) => {
    //   e.preventDefault();
    
    //   // Assuming the form has a single input with id="answer"
    //   const favoriteSport = e.target.elements.answer.value;
    
    //   // Assuming the form has an input with name="name" and an input with name="phone"
    //   const name = contact.name;
     
    //   // Save the data to Firestore
    //   firestore.collection("userData").add({
    //     favoriteSport: favoriteSport,
    //     name: name,
       
    //   })
    //   .then((docRef) => {
    //     console.log("Data added to Firestore with ID: ", docRef.id);
    //     // You can add any success message or redirect the user to another page after data submission.
    //   })
    //   .catch((error) => {
    //     console.error("Error adding data to Firestore: ", error);
    //     // Handle error scenario here, show error message to the user, etc.
    //   });
    // };
   

    

  return (
    <>
      <div className="p-4 box mt-3 text-center">
        Hello Welcome <br />
        {user && user.email}
      
      </div>
      <div className="d-grid gap-2">
        <div className="p-4 box mt-3 text-center">
          <h4 text="text-center">What is your favourite sport?</h4>
          <Form >
            <input type = "text" id="answer" placeholder="Type here..."></input>
            <button >Submit</button>
          </Form
          >
          <Form className="card">
            <input type="text" name = "name" value={contact.name} onChange={handleChange}
             placeholder="YourName"
            ></input>
          
            <button>Submit</button>
          </Form>
        </div>
        <Button variant="primary" onClick={handleLogout} >
          Log out
        </Button>
      </div>
    </>
  );
};

export default Home;